<template>
  <div id="single-blog">
    <h1>{{blog.title}}</h1>
    <article>{{blog.content}}</article>
    <p>Author: {{blog.author}}</p>
    <p>Categories: </p>
    <ul>
      <li v-for="category in blog.categories">{{category}}</li>
    </ul>
    <button @click="deleteSingleBlog()">delete</button>
  </div>
</template>

<script>
export default {
  name: "single-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {}
    }
  },
  created() {
    this.$http.get('https://vuedemo-2ba3a.firebaseio.com/posts/' + this.id + '.json')
    .then(function(data) {
      return data.json();
    })
    .then(function(data){
      this.blog = data;
    })
  },
  methods: {
    deleteSingleBlog() {
      this.$http.delete("https://vuedemo-2ba3a.firebaseio.com/posts/" + this.id + '.json')
      .then(response => {
        this.$router.push({path: '/'});
      })
    }
  }
}
</script>

<style>
  #single-blog {
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eee;
    border: 1px dotted #aaa;
  }
</style>



